export default {
  methods: {
    /**
     * Remove tools in DOM
     * @param {className} Element
     */
    removeElementsByClass(className) {
      var elements = document.getElementsByClassName(className);
      while (elements.length > 0) {
        elements[0].parentNode.removeChild(elements[0]);
      }
    },

    // Copy the corresponding user tool content to all remaining tools.
    copyTool() {
      this.parent_ref.tools.forEach((tool) => {
        if (tool.component?.inner_component_name == this.$options.name && tool.user_id == this.parent_ref.active_users_list[0].id) {
          this.$set(tool.props, "value", this.$options.name == "AcEsignToolSignature" ? this.signature_src : this.input_value);
          this.$set(tool.props, "width", this.resize_props.width);
          this.$set(tool.props, "height", this.resize_props.height);
        }
      });
      this.removeElementsByClass("ac-esign-tool-wrapper");

      this.parent_ref.load_tools(true);
    },
  },
};
